.project_text {
  text-align: center;
  background: #ebecf1;
  min-height: 100px;
}

.project_text P {
  color: black;
  font-weight: 600;
  font-size: 18px;
}

.link a {
  text-decoration: none;
  color: blue;
  font-weight: 600;
}

.link a:hover {
  color: red;
  transition: linear 0.2s;
}

.projects {
  display: flex;
  justify-content: space-around;
  height: 100vh;
  width: 100vw;
}

.cardtitle {
  color: #000;
  height: 200px;
  background: url("https://pomelnikov.com/images/obuchenie/Pomelnikov-htmlcss.jpg")
    no-repeat center;
  background-size: cover;
}

.title_pic {
  color: #000;
  height: 200px;
  background: url("data:image/png;base64,")
    no-repeat center;
  background-size: cover;
}
